.BuildControl{
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 5px;

}

.BuildContol button{
      display: block;
      font: inherit;
      padding: 5px;
      margin: 0 5px;
      width: 80px;
      border: 1px solid #aa6817;
      cursor: pointer;
      outline: none;

}

.BuildControl button:disabled{
      background-color: #ac9980;
      border: 1px solid #7e7365;
      color: #ccc;
      cursor: default;
}

.BuildControl button:hover{
      background-color: #ac9980;
      color: #ccc;
      cursor: not-allowed;

}


.Label {
      padding: 10px;
      font-weight: bold;
      width: 100px;
      text-align: right;
}

.BuildControl .Less{
      background-color: #d39952;
      color: white;
}

.BuildControl .More{
      background: #8f5e1e;
      color: white;
}


