.Modal{
      position: fixed;
      z-index: 700;
      background-color: white;
      width: 70%;
      border: 1px solid #ccc;
      box-shadow: 1px 1px 1px black;
      padding: 16px;
      left: 15%;
      top: 35%;
      transition:  all 0.8s ease-out;
}

@media (min-width:600px){
      .Modal{
            width: 500px;
            left: calc(50% - 250px);
      }
}