.SideBar{
      width: 280px;
      background-color: white;
      position: fixed;
      top: 0;
      left: 0;
      height: 100%;
      z-index: 200;
      padding: 32px 16px;
      transition: transform 0.5s ease-out;
}

@media(min-width:500px){
      .SideBar{
            display: none;
      }
}

.Logo{
      height: 15%;
}
.Open{
      transform: translateX(0);
}

.Close{
      transform: translateX(-100%);
}