.HamburgerMenu {
      width: 40px;
      height: 100%;
      display: flex;
      flex-flow: column;
      justify-content: space-around;
      padding: 10px 0;
      box-sizing: border-box;
}

.HamburgerMenu div {
      background-color: white;
      height: 3px;
}

@media (min-width: 600px){
      .HamburgerMenu{
            display: none;
      }
}